<template>
  <div class="chart-line">
    <div :id="id" class="pie" style="width: 100%; height: 100%;"></div>
  </div>
</template>
<script>
export default {
  props: {
    id: {
      // 
      default: function () {
        return "";
      }
    },
    chartType: {
      default: "line"
    },
    barWidth: {
      type: Number,
      default: 16
    },
    fontSize: {
      type: Number,
      default: 28
    },
    xAxis: {
      // x
      type: Array,
      default: function () {
        return [];
      }
    },
    yAxis: {
      type: Array,
      default: function () {
        // return [1600, 2300, 1200, 1600, 500, 1200, 1600, 200, 1200];
        return [];
      }
    },
    yMaxData: {
      type: Number,
      default: function () {
        return 0;
      }
    },
    //   
    colorArrs: {
      type: Array,
      default: function () {
        return  ["#32F1FE", "rgba(49, 238, 250, 0.20)"] 
      }
    },
    // y
    yType: {
      type: String,
      default: function () {
        return 'number'       //   ratio
      }
    },
    // y
    yMaxNumber: {
      type: String,
      default: function () {
        return '1000'
      }
    },
    // y
    showLaber: {
      type: Boolean,
      default: function () {
        return true
      }
    }
  },
  data: function () {
    return {
      echartsOption: null,
      screenWidth: document.body.clientWidth
    };
  },
  computed: {},
  watch: {
    yAxis: {
      deep: true,
      handler: function () {
        this.initChart();
      }
    },
    screenWidth(val) {
      this.screenWidth = val;
      this.echartsOption.resize()
    }
  },
  activated() { },
  mounted() {
    const self = this
    // domecharts
    window.onresize = () => {
      return (() => {
        window.screenWidth = document.body.clientWidth
        self.screenWidth = window.screenWidth
      })()
    }
    this.initChart();
  },
  methods: {
    newline(option, number, axis) {
      /* json */
      option[axis][0]["axisLabel"] = {
        interval: 0,
        fontSize: this.fontSize,
        color: "#fff",
        // 
        formatter: function (params) {
          var newParamsName = "";
          var paramsNameNumber = params.length;
          var provideNumber = number;
          var rowNumber = Math.ceil(paramsNameNumber / provideNumber);
          if (paramsNameNumber > provideNumber) {
            for (var p = 0; p < rowNumber; p++) {
              var tempStr = "";
              var start = p * provideNumber;
              var end = start + provideNumber;
              if (p == rowNumber - 1) {
                tempStr = params.substring(start, paramsNameNumber);
              } else {
                tempStr = params.substring(start, end) + "\n";
              }
              newParamsName += tempStr;
            }
          } else {
            newParamsName = params;
          }
          return newParamsName;
        }
      };
      return option;
    },
    // 
    computeRatio() {
      let sum = 0
      let vm = this.yAxis
      let target = []
      sum = vm.reduce((prev, curr) => {
        return prev + curr
      })
      for(const i in this.yAxis) {
        let _vm = this.yAxis[i]
        target.push(
          ((_vm/sum)*100).toFixed(0)
        )
      }
      return target
    },
    initChart() {
      // y   
      let yTypeShow = ''        // '{value}%' y
      let yMax = ''             // y
      let yAxisData = []  // 
      if (this.yType == 'number') {
        yTypeShow = '{value}'
        // y
        yMax = this.yMaxNumber
        yAxisData = this.yAxis  // y
      } else if (this.yType == 'ratio') {
        yTypeShow = '{value}%'        //  value  100
        yMax = '100'
        yAxisData = this.yAxis  // y
        // let showYdata = this.computeRatio()
        // console.log("baifenbi", showYdata)
        // yAxisData = showYdata 
      }
      //
      let nowClientWidth = document.documentElement.clientWidth;
      // 
      let nowSize = function (val, initWidth = 1920) {
        return val * (nowClientWidth / initWidth);
      };
      let colors = ["#3979E9", "#fff", "#01EEF6", "#2D99FF", "#1B3C96"];
      this.echartsOption = this.$echarts.init(
        document.getElementById(this.id)
      );
      this.echartsOption.clear();
      // this
      let that = this;
      let option = {
        grid: {
          top: "12%",
          bottom: "15%",
          left: "10%",
          right: "10%"
        },
        xAxis: [
          {
            type: "category",
            data: this.xAxis,
            // max: 7,
            axisLine: {
              lineStyle: {
                color: colors[4],   // x 
                width: nowSize(1),
              }
            },
            // 
            // axisLabel: {
            //   show: true,
            //   interval: 0, // x
            //   textStyle: {
            //     color: colors[4],
            //     fontWeight: 600,
            //     fontSize: nowSize(this.fontSize)
            //   }
            // },
            axisLabel: {  
              interval: 0,  
              margin: nowSize(10),
              textStyle: {
                color: '#fff',
                fontWeight: 600,
                fontSize: nowSize(14)
              },
              formatter:function(value)  
              {   
                  var ret = "";//\n  
                  var maxLength = 5;//  
                  var valLength = value.length;//X  
                  var rowN = Math.ceil(valLength / maxLength); //  
                  if (rowN > 1)//3,  
                  {  
                      for (var i = 0; i < rowN; i++) {  
                          var temp = "";//  
                          var start = i * maxLength;//  
                          var end = start + maxLength;//  
                          //  
                          temp = value.substring(start, end) + "\n";  
                          ret += temp; //  
                      }  
                      return ret;  
                  }  
                  else {  
                      return value;  
                  }  
              }  
            },
            axisTick: {
              show: true,
              inside: true,
              length: nowSize(5)
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            scale: true,
            min: 0,
            max: yMax,
            splitNumber: 5,
            axisLine: {
              show: false,
              lineStyle: {
                color: colors[2],
                width: nowSize(5)
              }
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              show: true,
              margin: nowSize(30),
              formatter: yTypeShow,
              textStyle: {
                // y
                color: "#ffffff",
                fontSize: nowSize(this.fontSize),
                fontWeight: 600, 
              }
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: colors[4]
              }
            }
          }
        ],
        series: [
          {
            type: this.chartType,
            barWidth: nowSize(this.barWidth),
            // itemStyle: {
            //   color: colors[2]
            // },
            itemStyle: {
              normal: {
                color: function (params) {
                  let colorArr = that.colorArrs;
                  return new that.$echarts.graphic.LinearGradient(
                    0,
                    0,
                    0,
                    1,
                    [
                      {
                        offset: 0,
                        color: colorArr[0] // 0% 
                      },
                      {
                        offset: 1,
                        color: colorArr[1] // 100% 
                      }
                    ],
                    false
                  );
                }
                // barBorderRadius: [30,30,0,0]
              }
            },
            lineStyle: {
              color: colors[2],
              width: nowSize(5)
            },
            label: {
              normal: {
                show: this.showLaber,
                position: "top",
                color: "#00FFFF",
                fontSize: nowSize(14),
                // fontWeight: 600
              }
            },
            symbolSize: nowSize(20),
            data: yAxisData
          }
        ]
      }; 
      // option = this.newline(option, 5, "xAxis");
      this.echartsOption.setOption(option);
    }
  }
};
</script>
<style lang="scss">
.chart-line {
  position: relative;
  width: 100%;
  height: 100%;
  .pie {
    width: 100%;
    height: 100%;
  }
}
</style>
