<script>
import Vue from "vue";
import { mapGetters } from "vuex";
import AMapLoader from "@/utils/aMap.js";
export default {
  name: "aMap",
  components: {},
  watch: {},
  computed: {},
  data() {
    return {
      circleTip: [
        { title: "", type: "0", image: "circle1" },
        { title: "", type: "1", image: "circle2" },
      ],
      markerStyles: null,
      massMarks: null,
      tableData: [],
      clickItem: [],
      markDatas: null,
      infoWindow: null,
      map: null,
    };
  },
  computed: {
    // getNumberTip() {
    //   return "";
    // }
  },
  methods: {
    // showAreaData(id) {},
    /*
    22.568283,113.895858 
    22.580897,113.883902 
    22.628623,113.841914 
    22.671404,113.822755 
    22.688173,113.794997 
    22.733314,113.811647 
    22.743085,113.829852 
    22.764615,113.848396 
    22.798532,113.866233 
    22.681693,113.939388 */
    showData() {
      var data = [
        { latitude: 22.568283, longitude: 113.895858, areaName: "" ,companyNum:12,fee:254},
        { latitude: 22.580897, longitude: 113.883902, areaName: "" ,companyNum:8,fee:124},
        { latitude: 22.628623, longitude: 113.841914, areaName: "" ,companyNum:9,fee:334},
        { latitude: 22.671404, longitude: 113.822755, areaName: "" ,companyNum:12,fee:124},
        { latitude: 22.688173, longitude: 113.794997, areaName: "" ,companyNum:11,fee:94},
        { latitude: 22.733314, longitude: 113.811647, areaName: "" ,companyNum:13,fee:234},
        { latitude: 22.743085, longitude: 113.829852, areaName: "" ,companyNum:11,fee:141},
        { latitude: 22.764615, longitude: 113.848396, areaName: "" ,companyNum:14,fee:222},
        { latitude: 22.798532, longitude: 113.866233, areaName: "" ,companyNum:6,fee:101},
        { latitude: 22.681693, longitude: 113.939388, areaName: "" ,companyNum:9,fee:99},
      ];
      var tData = [];
      let that = this;
      var markerData = [];
      var styleIndex = 0;
      this.markDatas = data;
    
      data.forEach((element) => {
        tData.push({
          name: element.areaName,
          count: 100,
        });

        if (
          element.longitude != undefined &&
          element.latitude != undefined &&
          element.longitude != null &&
          element.latitude != null
        ) {
          var lat = parseFloat(element.latitude);
          var lon = parseFloat(element.longitude);

          console.log(`${lat},${lon}`);

          markerData.push({
            lnglat: [lon, lat],
            id: data.indexOf(element),
            style: styleIndex, // styleObjectArr
          });
        }
      });
      this.tableData = tData;
      if (this.massMarks != null) {
        this.massMarks.clear();
      }
      this.massMarks = new AMap.MassMarks(markerData, {
        zIndex: 111,
        style: this.markerStyles,
      });
      this.massMarks.on("click", (e) => {
        console.log(e);
        var element = this.markDatas[e.data.id];
        this.clickItem = [
          {
            name:"",
            value:element.areaName
          },
          {
            name:"",
            value:element.companyNum+""
          },
          {
            name:"",
            value:element.fee+""
          }
        ];
        console.log(this.clickItem);
        //this.$refs.pop

        this.infoWindow = new AMap.InfoWindow({
          isCustom: true, //
          content: this.$refs.pop, // dom  html 
        });
        this.infoWindow.open(this.map, e.data.lnglat);
      });
      //  massMarks 
      this.massMarks.setMap(this.map);
    },
  },
  mounted() {
    let that = this;

    console.log("preInitMap");
    // this.getMapData(this.circleTip[0].type)
    Promise.all([AMapLoader()]).then((values) => {
      console.log("values:", values);
      this.map = new AMap.Map("map", {
        zoom: 11, //
        mapStyle: "amap://styles/ec036383cbc0a7d1c55b52d242a1d57c",
        center: [113.8821168109775, 22.597056195463144],
      });

      this.markerStyles = [
        {
          url: "https://a.amap.com/jsapi_demos/static/images/mass0.png", // 
          size: new AMap.Size(11, 11), // 
          anchor: new AMap.Pixel(5, 5), // 
        },
        {
          url: "https://a.amap.com/jsapi_demos/static/images/mass1.png", // 
          size: new AMap.Size(11, 11), // 
          anchor: new AMap.Pixel(5, 5), // 
        },
      ];
      // if (values.length == 2 && values[1].code == 200) {
        console.log("success");
        this.showData();
      // }

      // this.massMarks = new AMap.MassMarks({
      //   zIndex: 5, // 
      //   zooms: [3, 19], // 
      //   style: this.markerStyles //
      // });
    });
  },
};
